import React, { useState, useRef } from "react";
import ReCAPTCHA from "react-google-recaptcha";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import Axios from "axios";
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const Bottom4 = () => {
  const MySwal = withReactContent(Swal);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [number, setNumber] = useState(0);
  const [message, setMessage] = useState("");

  const [nameLength, setNameLength] = useState(false);
  const [emailInvalid, setemailInvalid] = useState(false);
  const [messageLength, setmessageLength] = useState(false);

  const messageForm = document.getElementById("form-messagehome");
  const phoneForm = document.getElementById("form-phone");
  const nameForm = document.getElementById("form-name");
  const emailForm = document.getElementById("form-email");


  const ValidateEmail = (e) => {
    var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
    if (e.match(mailformat)) {
      return true;
    } else {
      return false;
    }
  }

  const submitForm = (e) => {
    e.preventDefault();
    setNameLength(false);
    setemailInvalid(false);
    setmessageLength(false);

    if(name.length <= 5 && !ValidateEmail(email)){
      setNameLength(true);
      setemailInvalid(true);
    }
    else if (name.length <= 6) {
        setNameLength(true);
      } 
      else if(!ValidateEmail(email)){
        setemailInvalid(true);
      }
      else if(message.length <= 10){
        setmessageLength(true);
      }
      else {
        Axios.post('https://test.are-ai.ae/contact',{
          name:name,
          email:email,
          number:number,
          message:message
        }).then(()=>{
          MySwal.fire({
            title: <strong>Message sent</strong>,
            html: <p>Thanks for reaching out to us! You should hear back from us in the next 24 hours. but if not, let us know! </p>,
            icon: 'success',
            confirmButtonText: 'Close',
            iconColor: '#135CFD',
          }).then((result) =>{
            if(result.isConfirmed){
              document.getElementById("form-contactus").reset(); 
              setName("");
              setEmail("");
              setNumber("");
              setMessage("");
            }
          })
        })
    }
  };

  return (
    <div className="wrapper">
      <div className="btm4_con">
        <div className="btm4_info">
          <h3>Reach Out to Us</h3>
          <h2>Drop Us a Message</h2>
          <p>
           To help us make our services better, let us know about your opinions and concerns.
          </p>
        </div>
        <div className="btm4_form">
          <Box
            className="form-home"
            component="form"
            noValidate
            autoComplete="off"
            id="form-contactus"
          >
            <div className="two-column">
              <section>
                <div id="name-valdation">
                  <TextField
                    error={nameLength}
                    helperText={nameLength ? "Please enter your full name." : ""}
                    id="form-name"
                    label="Full Name *"
                    value={name}
                    onChange={(e) => {
                      setName(e.target.value);
                      (name.length <= 6 ?  setNameLength(true) :  setNameLength(false))
                    }}
                  />
                </div>
              </section>
              <section>
                <TextField
                  error={emailInvalid}
                  helperText={emailInvalid ? "Please enter a valid email address." : ""}
                  id="form-email"
                  label="Email Address *"
                  value={email}
                  onChange={(e) => {
                    setEmail(e.target.value);
                    (!ValidateEmail(email) ?  setemailInvalid(true) :  setemailInvalid(false))
                  }}
                />
              </section>
            </div>
            <div className="two-column column-message">
              <section>
                <TextField
                  id="form-phone"
                  label="Telephone Number *"
                  type="number"
                  onChange={(e) => {
                    setNumber(e.target.value);
                  }}
                  onInput={(e) =>{
                    e.target.value = e.target.value.replace(/[^0-9.]/g, '').replace(/(\..*?)\..*/g, '$1')
                  }}
                />
              </section>
              <section>
                <TextField
                  error={messageLength}
                  helperText={messageLength ? "Please enter a message more than 10 characters" : ""}
                  id="form-messagehome"
                  label="Message(s)"
                  onChange={(e) => {
                    setMessage(e.target.value);
                    (message.length <= 10 ?  setmessageLength(true) :  setmessageLength(false))
                  }}
                />
              </section>
            </div>
            <div className="form-box-btm4">
              {/* <ReCAPTCHA
                sitekey="6LfdRnojAAAAAHW10drAfdiMf0FrEQgnHAFIKo1p"
                className="recaptcha"
              /> */}

              <div className="btm4-submit">
                <input type="submit" value="Submit" onClick={submitForm} />
              </div>
            </div>
          </Box>
        </div>
      </div>
    </div>
  );
};

export default Bottom4;
