import React, { useState, useEffect } from "react";
import "../style.css";
import { Link } from "react-router-dom";
import { setGlobalState, useGlobalState } from "./state/menu-state";
import MainLink from "./links/MainLink";

function Header({scrollTop}) {
  const [defaultState] = useGlobalState("isActive");
  const [url, setUrl] = useState("");
  const [isActive, setActive] = useState(false);
  const [isOpened, setOpened] = useState(false);

  const body = document.getElementById("body");
  const root = document.getElementById("root");
  const sidebar = document.getElementById("sidebar-contact");


  useEffect(() => {
    const location = window.location.pathname;
    setUrl(location);
    setActive(defaultState);
  }, []);

  
  const toggleMenu = () => {
    setActive(!isActive);
    setGlobalState("isActive", !isActive);

  };


  const showSidebar = () => {
    setOpened(!isOpened);
    setGlobalState("showSidebar", !isOpened);

    root.classList.toggle("backdrop");
    body.classList.toggle("overflowHidden");
    sidebar.classList.add("sidebar-active");

  };

 

  return (
 
       <div className="wrapper_fluid">
      <div className="header_con">
        <div className="header_info">
          <div className="header_navigation">
            <div className="main_logo">
              <Link to="/" onClick={scrollTop}>
                <h2>
                  LOGO <span>HERE</span>
                </h2>
              </Link>
            </div>
            <div className="page_nav">
                <MainLink url={url} />

              <div className="page_right_info">
                <button
                  className={isActive ? "page_nav_button-opened": "page_nav_button"}
                  type="submit"
                  onClick={toggleMenu}
                >
                  <section
                    className={isActive ? "menu-line-closed" : "menu-line"}
                  >
                    <span></span>
                    <span>
                      <section className="menu-three-line">
                        <div></div>
                        <div></div>
                        <div></div>
                      </section>
                    </span>
                    <span></span>
                  </section>
                  <section
                    className={!isActive ? "close-line" : "close-line-opened"}
                  >
                    <figure>
                      <img src="/assets/images/x-icon.svg" alt="close icon" />
                    </figure>
                  </section>
                </button>
                <div className="contact_btn">
                  <a onClick={showSidebar} className={ isActive ? "social-btn-opened" : "social-btn" }>
                    <span>Let's Talk</span>
                  </a>
                  <a onClick={showSidebar} className={isActive ? "social-btn2-opened" : "social2-btn"}>
                      <div>

                      </div>
                      <span>Let's Talk</span>
                  </a>
                </div>
                
              </div>
            </div>
          </div>

        
        </div>
      </div>
    </div>

 
  );


}


export default Header;
