import React, { useState, useEffect, useRef } from "react";
import Axios from "axios";
import Sidenav from "../components/Sidenav";
import "../../css/are.css";
const OnlineForms = () => {
  const [messageList, setmessageList] = useState([]);
  const [current, setCurrent] = useState(0);
  const [MessageID, setMessageID] = useState("");
  const [isRead, setisRead] = useState("");
  const message = useRef(null);
  const messageNotActive = useRef(null);

  useEffect(() => {
    Axios.get("http://v001.are-ai.ae/online-forms", {}).then((response) => {
      setmessageList(response.data);
    });
  }, []);
  const readNotif = () => {
    Axios.put("http://v001.are-ai.ae/online-forms", {
      MessageID: MessageID,
      isRead: isRead,
    }).then((response)=>{
      
    });
  };

  return (
    <>
      <div id="are-main">
        <div className="are-container">
          <Sidenav />

          <div className="are-parent-right">
            <div className="are-parentright-con">
              <div className="are-main-top text-left heading">
                <h2>Form Database</h2>
              </div>
              <div className="are-parent-message">
                <div className="are-left-message">
                  {messageList.map((info, index) => {
                    return (
                      <section
                        data-id={info.MessageID}
                        key={index}
                        ref={index === current ? message : messageNotActive}
                        className={
                          index == current
                            ? "are-msg-section active-section"
                            : info.IsRead == 1
                            ? "are-msg-section isRead"
                            : "are-msg-section"
                        }
                        onClick={() => {
                          setCurrent(index);
                          setMessageID(info.MessageID);
                          setisRead("0");
                          readNotif();
                        }}
                      >
                        <figure>
                          <img
                            src="/assets/images/dummy-profile.png"
                            alt="dummy icon"
                          />
                        </figure>
                        <div className="are-msg-info-text">
                          <h2>{info.Fullname}</h2>
                          <p> {info.Formname}</p>
                          <span>
                            {info.Month} {info.Day}
                          </span>
                        </div>
                      </section>
                    );
                  })}
                </div>
                <div className="are-right-message">
                  {messageList.map((info, index) => {
                    return (
                      <section
                        data-id={info.MessageID}
                        key={index}
                        ref={index === current ? message : messageNotActive}
                        className={
                          index == current
                            ? "are-rightmsg-section active-section"
                            : "are-rightmsg-section"
                        }
                      >
                        <div className="are-msg-info-text">
                          <h2>{info.Formname}</h2>
                          <p>
                            Date Received:{" "}
                            <span>
                              {info.Month} {info.Day}, {info.Year}
                            </span>
                          </p>
                          <p>
                            Full Name: <span>{info.Fullname}</span>
                          </p>
                          <p>
                            Email Address: <span>{info.Email}</span>{" "}
                          </p>
                          <p>
                            Contact Number: <span>{info.Number}</span>{" "}
                          </p>
                          <p className="are-message-span">
                            Question or Message: <span>{info.Message}</span>{" "}
                          </p>
                        </div>
                      </section>
                    );
                  })}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default OnlineForms;
