import React, { useState, useEffect, useTransition } from "react";
import { setGlobalState, useGlobalState } from "../state/menu-state";

import ContactInfo from "./ContactInfo";
import { Link } from "react-router-dom";

const Sidebar = () => {
  const [isPending, startTransition] = useTransition();
  const [sideBar] = useGlobalState("showSidebar");
  const [menuActive] = useGlobalState("isActive");

  const [isActive, setActive] = useState(false);
  const [isOpened, setOpened] = useState(false);
  const body = document.getElementById("body");
  const root = document.getElementById("root");
  const sidebar = document.getElementById("sidebar-contact");
  const dropMenu = document.getElementsByClassName("page_nav_button-opened");

  const hideSidebar = () => {
    setOpened(isOpened);
    setGlobalState("showSidebar", isOpened);
    sidebar.classList.remove("sidebar-active");
    sidebar.classList.remove("sidebar-closed");
    sidebar.classList.add("sidebar-closed");

    root.classList.remove("backdrop");
    body.classList.remove("overflowHidden");
  };

  const hideBacktoTop = () => {
    setOpened(isOpened);
    setGlobalState("isActive", isActive);
    setGlobalState("showSidebar", isOpened);
    startTransition(() => {
      sidebar.classList.remove("sidebar-active");
      sidebar.classList.remove("sidebar-closed");
      sidebar.classList.add("sidebar-closed");

      root.classList.remove("backdrop");
      body.classList.remove("overflowHidden");
      dropMenu.classList.remove("social-btn-opened");
      dropMenu.classList.add("social-btn");
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    });
  };

  return (
    <>
      <div id="sidebar-contact" className="sidebar-closed">
        <div className="sidebar_info">
          <button type="submit" onClick={hideSidebar}>
            {" "}
          </button>

          <div className="sidebar-header">
            <p>Let's talk</p>

            <h2>
              Our Experts Understand, guide, and conceretize your IT projects
              with custom solutions!
            </h2>

            <Link to="/contact-us" onClick={hideBacktoTop}>
              {" "}
              <span>Let's Talk</span>
            </Link>
          </div>
          <div className="sidebar-footer">
            <ContactInfo />
          </div>
        </div>
      </div>
    </>
  );
};

export default Sidebar;
