import "./style.css";
import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Home from "./pages/Home";
import Services from "./pages/Services";
import About from "./pages/About";
import ErrorPage from "./pages/404";

import ITSolutions from "./pages/ITSolutions";
import Contact from "./pages/Contact";
import OnlineForms from "./login/admin/OnlineForms";
import Login from "./login/Login";


function App() {
  const [getUrl, setUrl] = useState("");

  const hideLoader = () => {
    const loader = document.getElementById("loader-animate");
    loader.classList.remove("loader-animate");
    loader.classList.add("hidden");
  };
  const noLoader = () => {
    const loader = document.getElementById("loader-animate");
    loader.classList.add("hidden");
  }
  useEffect(() => {
    const loader = document.getElementById("loader-animate");
    const url = window.location.pathname;
    setUrl(url);

    if(url === "/" || url === "/it-solutions" || url === "/our-offers"  || url === "/about-us" || url === "/contact-us"  === "/online-forms"){
      setTimeout(hideLoader, 3600)
    }else{
      noLoader();
    }
    

 
  }, [getUrl]);

  return (
    <Router>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/it-solutions" element={<ITSolutions />} />
        <Route path="/our-offers" element={<Services />} />
        <Route path="/about-us" element={<About />} />
        <Route path="/contact-us" element={<Contact />} />
        <Route path="/online-forms" element={<OnlineForms />} />
      
        <Route path="/login" element={<Login />} />
        
        <Route path="*" element={<ErrorPage />} />
      </Routes>

      <div className="loader-animate" id="loader-animate">
        <div className="loader-info">
          <h2>
            SAMPLE
            <span>LOGO</span>
            <span>HERE</span>
          </h2>
        </div>
      </div>

      {/* <div className={getUrl != "/" ? "nonhome-loader visible" : "hidden"} id="nh-loader">
     <div className={getUrl != "/" ? "loader-info loader-load" : ""} id="nhloader-info">
          <h2>
            SAMPLE 2<span>LOGO HERE</span>
          </h2>
        </div>
    </div> */}
    </Router>
  );
}
export default App;
