import React, { useEffect, useState } from "react";
import Header from "../components/Header";
import Footer from "../components/Footer";
import Menu from "../components/Menu";
import Sidebar from "../components/includes/Sidebar";

const ErrorPage = () => {
  const [title, setTitle] = useState("404 | ARE Transforming Together");

  useEffect(() => {
    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
    document.title = title;
  }, []);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  return (
    <div className="nh-container">
      <div id="header">
        <Header scrollTop={scrollToTop} />
      </div>
      <div id="non-home">
        <div className="wrapper">
          <div className="non-home-con align-center">
            <div className="non-home-info text-center highlight error-page">
              <h2>
                {" "}
                <span>404</span> PAGE NOT FOUND!
              </h2>
              <p>
                We are excited to post contents on the website. This space is
                reserved.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div id="footer">
        <Footer />
      </div>
      <Menu />
      <Sidebar />
    </div>
  );
};

export default ErrorPage;
