import React,{useState} from "react";
import { Link } from "react-router-dom";
import { setGlobalState, useGlobalState } from "../state/menu-state";

const MainLink = ({ url  }) => {
  const [isActive, setActive] = useState(false);

  const scrollToTop = () => {
    document.getElementById('body')?.scrollIntoView({ behavior: 'smooth' });
    setActive(isActive);
    setGlobalState("isActive", isActive);
  };

  return (
    <>
      <ul>
      <li className={url == "/" ? "active-page-main" : ""}>
          <Link to="/" onClick={scrollToTop}>Home</Link>
        </li>
        <li className={url == "/about-us" ? "active-page-main" : ""}>
          <Link to="/about-us" onClick={scrollToTop}>About Us</Link>
        </li>
        <li className={url == "/it-solutions" ? "active-page-main" : ""}>
          <Link to="/it-solutions" onClick={scrollToTop}>IT Solutions</Link>
        </li>
        <li className={url == "/our-offers" ? "active-page-main" : ""}>
          <Link to="/our-offers" onClick={scrollToTop}>Our Offers</Link>
        </li>
      
     
      
       
       
      </ul>
    </>
  );
};

export default MainLink;
