import React, { useEffect, useState } from "react";
import "../style.css";
import Header from "../components/Header";
import Banner from "../components/includes/Banner";
import Middle from "../components/includes/Middle";
import { MiddleBoxes } from "../components/boxes-data/Middle-Data";
import Main from "../components/includes/Main";
import Bottom from "../components/includes/Bottom";
import Bottom2 from "../components/includes/Bottom2";
import { BottomBoxes } from "../components/boxes-data/Bottom2-Data";
import Bottom3 from "../components/includes/Bottom3";
import Bottom4 from "../components/includes/Bottom4";
import Footer from "../components/Footer";
import Menu from "../components/Menu";
import Sidebar from "../components/includes/Sidebar";
import { setGlobalState } from "../components/state/menu-state";

function Home() {
  const [title, setTitle] = useState("ARE Transforming Together");
  const [isActive, setActive] = useState(false);
  useEffect(() => {
    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
    document.title = title;
  }, []);
  const scrollToTop = () => {
    setActive(isActive);
    setGlobalState("isActive", isActive);
    document.getElementById("body")?.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <div id="page">
      <div id="header">
        <Header scrollTop={scrollToTop} />
      </div>

      <div id="banner">
        <Banner />
      </div>

      <div id="middle">
        <Middle content={MiddleBoxes} />
      </div>

      <div id="main">
        <Main />
      </div>

      <div id="bottom1">
        <Bottom />
      </div>

      <div id="bottom2">
        <Bottom2 content={BottomBoxes} />
      </div>
      <div id="bottom3">
        <Bottom3 />
      </div>
      <div id="bottom4">
        <Bottom4 />
      </div>
      <div id="footer">
        <Footer />
      </div>
      <Menu />
      <Sidebar />
    </div>
  );
}

export default Home;
