import React, { useState, useEffect } from "react";
import {
  setGlobalState,
  useGlobalState,
} from "../../components/state/menu-state";
import { Link } from "react-router-dom";

const Sidenav = () => {
  const [defaultState] = useGlobalState("isActive");
  const [isActive, setActive] = useState(false);
  const [url, setUrl] = useState("");


  useEffect(() => {
    const location = window.location.pathname;
    setUrl(location);
  }, []);

  const toggleMenu = () => {
    setActive(!isActive);
    setGlobalState("isActive", !isActive);
  };

  return (
    <>
      <div className="are-parent-left">
        <div className="are-sidenav-con">
          <div className="are-sidenav-btn">
            <button
              className={
                isActive ? "page_nav_button-opened" : "page_nav_button"
              }
              type="submit"
              onClick={toggleMenu}
            >
              <section className={isActive ? "menu-line-closed" : "menu-line"}>
                <span></span>
                <span>
                  <section className="menu-three-line">
                    <div></div>
                    <div></div>
                    <div></div>
                  </section>
                </span>
                <span></span>
              </section>
              <section
                className={!isActive ? "close-line" : "close-line-opened"}
              >
                <figure>
                  <img src="/assets/images/x-icon.svg" alt="close icon" />
                </figure>
              </section>
            </button>
          </div>
          <div className="are-sidenav-menu">
            <ul>
              <li className={url == "/" ? "active-page-adm" : ""}>
                <Link>
                  <figure>
                    <img src="/assets/images/dashboard-icon.png" alt="dashboard msg" />
                  </figure>
                </Link>
              </li>
              <li className={url == "/" ? "active-page-adm" : ""}>
                <Link  >
                  <figure>
                    <img src="/assets/images/todo-icon.png" alt="todo icon" />
                  </figure>
                </Link>
              </li>
              <li className={url == "/online-forms" ? "active-page-adm" : ""}>
                <Link  >
                  <figure>
                    <img src="/assets/images/msg-icon.png" alt="icon msg" />
                  </figure>
                </Link>
              </li>
              <li className={url == "/" ? "active-page-adm" : ""}>
                <Link>
                  <figure>
                    <img src="/assets/images/profile-icon.png" alt="profile icon" />
                  </figure>
                </Link>
              </li>
              <li  className={url == "/" ? "active-page-adm" : ""}>
                <Link>
                  <figure>
                    <img src="/assets/images/settings-icon.png" alt="cog icon" />
                  </figure>
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </>
  );
};

export default Sidenav;
