import React, { useEffect, useState, useRef } from "react";
import Header from "../components/Header";
import Footer from "../components/Footer";
import Menu from "../components/Menu";
import Sidebar from "../components/includes/Sidebar";

import Main from "../components/includes/Main";
import Bottom from "../components/includes/Bottom";
import AboutNH from "../components/non-home/About";

const About = () => {
  const [title, setTitle] = useState("About Us | ARE Transforming Together");
  useEffect(() => {
    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
    document.title = title;
  }, []);
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  return (
    <div className="nh-container">
      <div id="header">
        <Header scrollTop={scrollToTop} />
      </div>
      <AboutNH />
      <div id="non-home-about">
        <div className="wrapper">
          <div className="non-home-about2-con">
            <div className="non-home-about2-info">
              <h2 className="heading-2">
                We value human, organizational, and operational intelligence,
                not just artificial.
              </h2>

              <div className="pl-4">
                <p>
                  We are excited to post contents on the website. This space is
                  reserved for more information about the company. These dummy
                  text is intended to show you the volume of content that will
                  be used on this particular page. Thank you for visiting our
                  website. These contents are temporary and are for display
                  purposes only.
                </p>
                <p>
                  We can post company information here,{" "}
                  <mark className="comp">ARE Transforming Together</mark>. Such
                  space is reserved for the reader to learn more about the
                  business, its products and services. We are using these
                  contents on the website.
                </p>
                <p>
                  You are reading dummy text as placeholders for this layout.
                  Contents are for display purposes only.
                </p>
                <p>Placeholder for bullet list:</p>
                <ul className="bullet">
                  <li>Contents are for display purposes only.</li>
                  <li>We can post company information here.</li>
                  <li>We are using these contents on the website.</li>
                  <li>
                    You are reading dummy text as placeholders for this layout.
                  </li>
                </ul>
                <h2 className="heading-extra">
                  Keep in Touch. Sample text only.
                </h2>
                <p>
                  {" "}
                  You are reading dummy text as placeholders for this layout.{" "}
                  <a href="">Contents are</a> for display purposes only.{" "}
                  <a href=""> Click Here</a>{" "}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div id="main">
        <Main scrollTop={scrollToTop} />
      </div>
      <div id="bottom1">
        <Bottom />
      </div>

      <div id="footer">
        <Footer />
      </div>
      <Menu />
      <Sidebar />
    </div>
  );
};

export default About;
