import React, { useEffect, useState, useRef } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);
const AboutNH = () => {
  const component = useRef(null);
  useEffect(() => {
    let ctx = gsap.context(() => {
      gsap.to(
        ".panel-one__image",
        {
          opacity: 0,
          scrollTrigger: {
            trigger: "#non-home",
            start: "top top",
            end: "bottom 10%",
            pin: true, // pin the trigger element
            scrub: true, // link the image fade to the scroll
          },
        },
        component
      );

      return () => ctx.revert();
    });
  }, []);
  return (
    <>
      <div id="non-home" ref={component}>
        <div className="nh-about-con-banner">
          <figure className="nh-image">
            <img
              src="/assets/images/nh-banner.jpg"
              className="panel-one__image"
            />
          </figure>
          <div className="nh-about-info1">
            <h2>BANNER HERE</h2>
            <p>We are excited to post contents on the website. </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default AboutNH;
