import React, { useEffect, useState, useRef } from "react";
import "../../style.css";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);

function Carousel({ images }) {
  //global var
  const [current, setCurrent] = useState(0);
  const [autoPlay, setAutoPlay] = useState(true);
  const[disable, setDisable] = useState(true);
  const container = document.querySelector(".rslides");
  const slider = useRef(null);
  const sliderNotActive = useRef(null);

  let timeOut = null;

  // useEffect(() => {
  //   //if auto play is true then execute setTimeout
  //   //if autoplay is false then it will remain as null | false
  //   // timeOut =
  //   //   autoPlay &&
  //   //   setTimeout(() => {
  //   //     slideRight();
  //   //   }, 5000);

     
  // });
  const slideLeft = () => {
    const tl = gsap.timeline({ paused: true, defaults: {duration: .4, ease: 'Circ. easeOut' } });

    tl.to(container, { clipPath: "circle(48.66% at 10% 50%)" })
      .to(container, { clipPath: "circle(13.3% at 20% 50%)" })
      .to(container, { clipPath: "circle(13.3% at 20% -100%)" })
      .to(container, { clipPath: "circle(0% at 20% -100%)" })
      .to(container, { clipPath: "circle(0% at 20% 150%)" })
      .to(container, { clipPath: "circle(13.3% at 20% 50%)" })
      .to(container, { clipPath: "circle(48.66% at 10% 50%)" });
    tl.play();

    if(disable ){
      setCurrent(current === 0 ? images.length - 1 : current - 1);
     }else{
      setTimeout(() => {
        setCurrent(current === 0 ? images.length - 1 : current - 1);
      }, 2000);
     }

  };
  const slideRight = () => {
    const tl = gsap.timeline({ paused: true, defaults: {duration: .4, ease: 'Circ. easeOut' } });
    tl.to(container, { clipPath: "circle(48.66% at 10% 50%)" })
      .to(container, { clipPath: "circle(13.3% at 20% 50%)" })
      .to(container, { clipPath: "circle(13.3% at 20% 130%)" })
      .to(container, { clipPath: "circle(0% at 20% 150%)" })
  
      .to(container, { clipPath: "circle(0% at 20% -100%)" })
      .to(container, { clipPath: "circle(13.3% at 20% -100%)" })
      .to(container, { clipPath: "circle(13.3% at 20% 50%)" })
      .to(container, { clipPath: "circle(48.66% at 10% 50%)" })
 

  

    tl.play();
   if(disable ){
    setCurrent(current == images.length - 1 ? 0 : current + 1);
   }else{
    setTimeout(() => {
      setCurrent(current == images.length - 1 ? 0 : current + 1);
    }, 2000);
   }
  
   
  };

  return (
    <div
      className={disable ? "slider slider-shadow" : "slider"}
      onMouseEnter={() => {
        setAutoPlay(false);
        clearTimeout(timeOut);
      }}
      onMouseLeave={() => {
        setAutoPlay(true);
      }}
    >
      <ul className={disable ? "" : "rslides"}>
        {images.map((image, index) => {
          return (
            <li
              //number of array ex: 0,1,2
              ref={index == current ? slider : sliderNotActive}
              key={index}
              className={
                index == current
                  ? "carousel_card carousel_card-active"
                  : "carousel_card"
              }
            >
              <figure>
                <img src={image.images} alt={image.title} />
              </figure>
            </li>
          );
        })}
      </ul>

      <div className={disable ? "carousel_buttons hidden": "carousel_buttons"}>
        <div className="carousel_arrow_left">
          <a onClick={slideLeft}>
            <span></span>
          </a>
        </div>
        <div className="carousel_arrow_right">
          <a onClick={slideRight}>
            <span></span>
          </a>
        </div>
      </div>
      <div className="carousel_pagination">
        <ul>
          {images.map((_, index) => {
            return (
              <li
                key={index}
                className={
                  index == current
                    ? "pagination_dot pagination_dot-active"
                    : "pagination_dot"
                }
                onClick={() => setCurrent(index)}
              ></li>
            );
          })}
        </ul>
      </div>
    </div>
  );
}

export default Carousel;
